'use client';

import { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabaseClient';
import { useRouter } from 'next/navigation';

export default function Dashboard() {
  const [user, setUser] = useState(null);
  const [websites, setWebsites] = useState([]);
  const [guestPost, setGuestPost] = useState({
    website_id: '',
    buyer: '',
    seller: '',
    link: '',
    content: ''
  });

  const router = useRouter();

  // Fetch user on mount
  useEffect(() => {
    supabase.auth.getUser().then(({ data: { user } }) => {
      if (!user) router.push('/login');
      else setUser(user);
    });
  }, [router]);

  // Fetch websites from Supabase
  useEffect(() => {
    const fetchWebsites = async () => {
      const { data, error } = await supabase.from('websites').select('*');
      if (!error) setWebsites(data);
    };
    fetchWebsites();
  }, []);

  // Handle guest post submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    const { error } = await supabase.from('guestposts').insert([{
      ...guestPost,
      date_posted: new Date()
    }]);

    if (!error) {
      alert('Guest post submitted successfully!');
      setGuestPost({ website_id: '', buyer: '', seller: '', link: '', content: '' });
    } else {
      alert('Error submitting guest post.');
    }
  };

  const handleLogout = async () => {
    await supabase.auth.signOut();
    router.push('/login');
  };

  if (!user) return <p>Loading...</p>;

  return (
    <div className="max-w-4xl mx-auto p-6">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Welcome, {user.user_metadata?.name || user.email}</h1>
        <button
          onClick={handleLogout}
          className="bg-red-500 text-white px-4 py-2 rounded"
        >
          Logout
        </button>
      </div>

      {/* Guest Post Form */}
      <form onSubmit={handleSubmit} className="space-y-4 mb-10">
        <h2 className="text-xl font-semibold">Add Guest Posting Link</h2>

        <select
          className="w-full border px-3 py-2 rounded"
          value={guestPost.website_id}
          onChange={(e) => setGuestPost({ ...guestPost, website_id: e.target.value })}
          required
        >
          <option value="">Select Website</option>
          {websites.map((site) => (
            <option key={site.id} value={site.id}>
              {site.url} ({site.niche})
            </option>
          ))}
        </select>

        <input
          type="text"
          placeholder="Buyer"
          className="w-full border px-3 py-2 rounded"
          value={guestPost.buyer}
          onChange={(e) => setGuestPost({ ...guestPost, buyer: e.target.value })}
          required
        />

        <input
          type="text"
          placeholder="Seller"
          className="w-full border px-3 py-2 rounded"
          value={guestPost.seller}
          onChange={(e) => setGuestPost({ ...guestPost, seller: e.target.value })}
          required
        />

        <input
          type="url"
          placeholder="Guest Post Link"
          className="w-full border px-3 py-2 rounded"
          value={guestPost.link}
          onChange={(e) => setGuestPost({ ...guestPost, link: e.target.value })}
          required
        />

        <textarea
          placeholder="Content Summary"
          className="w-full border px-3 py-2 rounded"
          value={guestPost.content}
          onChange={(e) => setGuestPost({ ...guestPost, content: e.target.value })}
        />

        <button
          type="submit"
          className="bg-blue-600 text-white px-4 py-2 rounded"
        >
          Submit Guest Post
        </button>
      </form>

      {/* Website List */}
      <div>
        <h2 className="text-xl font-semibold mb-2">Available Websites</h2>
        <ul className="list-disc pl-5">
          {websites.map(site => (
            <li key={site.id}>
              {site.url} — {site.niche} — {site.tagged ? 'Tagged' : 'Available'}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
